html,
body {
  height: 100%;
}

body {
  font-family: "Poppins", sans-serif;
  font-size: 14px;
  font-weight: 400;
  font-style: normal;
  line-height: 24px;
  color: #000;
  background-color: #ffffff;
}

h1, .h1,
h2,
.h2,
h3,
.h3,
h4,
.h4,
h5,
.h5,
h6,
.h6 {
  font-family: "Poppins", sans-serif;
  font-weight: 400;
  margin-top: 0;
  color: #000000;
}

p {
  font-family: "Poppins", sans-serif;
  font-size: 14px;
  font-weight: 400;
  line-height: 24px;
  margin-bottom: 15px;
  color: #333;
}

h1, .h1 {
  font-size: 36px;
  line-height: 42px;
}

h2, .h2 {
  font-size: 30px;
  line-height: 36px;
}

h3, .h3 {
  font-size: 24px;
  line-height: 30px;
}

h4, .h4 {
  font-size: 18px;
  line-height: 24px;
}

h5, .h5 {
  font-size: 14px;
  line-height: 18px;
}

h6, .h6 {
  font-size: 12px;
  line-height: 14px;
}

p:last-child {
  margin-bottom: 0;
}

a,
button {
  line-height: inherit;
  cursor: pointer;
  text-decoration: none;
  color: #000;
}

a,
button,
img,
input,
span {
  transition: all 0.3s ease 0s;
}

*:focus {
  outline: none !important;
}

a:focus {
  text-decoration: none;
  color: inherit;
  outline: none;
}

a:hover {
  text-decoration: none;
}

button,
input[type=submit] {
  cursor: pointer;
}

ul {
  margin: 0;
  padding: 0;
  list-style: outside none none;
}

